var articleData = '# marked\r\n\r\n> A full-featured markdown parser and compiler, written in JavaScript. Built\r\n> for speed.\r\n\r\n[![NPM version](https:\/\/badge.fury.io\/js\/marked.png)][badge]\r\n\r\n## Install\r\n\r\n``` bash\r\nnpm install marked --save\r\n```\r\n\r\n## Usage\r\n\r\nMinimal usage:\r\n\r\n```js\r\nvar marked = require(\'marked\');\r\nconsole.log(marked(\'I am using __markdown__.\'));\r\n\/\/ Outputs: <p>I am using <strong>markdown<\/strong>.<\/p>\r\n```\r\n\r\nExample setting options with default values:\r\n\r\n```js\r\nvar marked = require(\'marked\');\r\nmarked.setOptions({\r\n  renderer: new marked.Renderer(),\r\n  gfm: true,\r\n  tables: true,\r\n  breaks: false,\r\n  pedantic: false,\r\n  sanitize: false,\r\n  smartLists: true,\r\n  smartypants: false\r\n});\r\n\r\nconsole.log(marked(\'I am using __markdown__.\'));\r\n```\r\n\r\n### Browser\r\n\r\n```html\r\n<!doctype html>\r\n<html>\r\n<head>\r\n  <meta charset=\"utf-8\"\/>\r\n  <title>Marked in the browser<\/title>\r\n  <script src=\"lib\/marked.js\"><\/script>\r\n<\/head>\r\n<body>\r\n  <div id=\"content\"><\/div>\r\n  <script>\r\n    document.getElementById(\'content\').innerHTML =\r\n      marked(\'# Marked in browser\\n\\nRendered by **marked**.\');\r\n  <\/script>\r\n<\/body>\r\n<\/html>\r\n```\r\n\r\n## marked(markdownString [,options] [,callback])\r\n\r\n### markdownString\r\n\r\nType: `string`\r\n\r\nString of markdown source to be compiled.\r\n\r\n### options\r\n\r\nType: `object`\r\n\r\nHash of options. Can also be set using the `marked.setOptions` method as seen\r\nabove.\r\n\r\n### callback\r\n\r\nType: `function`\r\n\r\nFunction called when the `markdownString` has been fully parsed when using\r\nasync highlighting. If the `options` argument is omitted, this can be used as\r\nthe second argument.\r\n\r\n## Options\r\n\r\n### highlight\r\n\r\nType: `function`\r\n\r\nA function to highlight code blocks. The first example below uses async highlighting with\r\n[node-pygmentize-bundled][pygmentize], and the second is a synchronous example using\r\n[highlight.js][highlight]:\r\n\r\n```js\r\nvar marked = require(\'marked\');\r\n\r\nvar markdownString = \'```js\\n console.log(\"hello\"); \\n```\';\r\n\r\n\/\/ Async highlighting with pygmentize-bundled\r\nmarked.setOptions({\r\n  highlight: function (code, lang, callback) {\r\n    require(\'pygmentize-bundled\')({ lang: lang, format: \'html\' }, code, function (err, result) {\r\n      callback(err, result.toString());\r\n    });\r\n  }\r\n});\r\n\r\n\/\/ Using async version of marked\r\nmarked(markdownString, function (err, content) {\r\n  if (err) throw err;\r\n  console.log(content);\r\n});\r\n\r\n\/\/ Synchronous highlighting with highlight.js\r\nmarked.setOptions({\r\n  highlight: function (code) {\r\n    return require(\'highlight.js\').highlightAuto(code).value;\r\n  }\r\n});\r\n\r\nconsole.log(marked(markdownString));\r\n```\r\n\r\n#### highlight arguments\r\n\r\n`code`\r\n\r\nType: `string`\r\n\r\nThe section of code to pass to the highlighter.\r\n\r\n`lang`\r\n\r\nType: `string`\r\n\r\nThe programming language specified in the code block.\r\n\r\n`callback`\r\n\r\nType: `function`\r\n\r\nThe callback function to call when using an async highlighter.\r\n\r\n### renderer\r\n\r\nType: `object`\r\nDefault: `new Renderer()`\r\n\r\nAn object containing functions to render tokens to HTML.\r\n\r\n#### Overriding renderer methods\r\n\r\nThe renderer option allows you to render tokens in a custom manner. Here is an\r\nexample of overriding the default heading token rendering by adding an embedded anchor tag like on GitHub:\r\n\r\n```javascript\r\nvar marked = require(\'marked\');\r\nvar renderer = new marked.Renderer();\r\n\r\nrenderer.heading = function (text, level) {\r\n  var escapedText = text.toLowerCase().replace(\/[^\\w]+\/g, \'-\');\r\n\r\n  return \'<h\' + level + \'><a name=\"\' +\r\n                escapedText +\r\n                 \'\" class=\"anchor\" href=\"#\' +\r\n                 escapedText +\r\n                 \'\"><span class=\"header-link\"><\/span><\/a>\' +\r\n                  text + \'<\/h\' + level + \'>\';\r\n},\r\n\r\nconsole.log(marked(\'# heading+\', { renderer: renderer }));\r\n```\r\nThis code will output the following HTML:\r\n```html\r\n<h1>\r\n  <a name=\"heading-\" class=\"anchor\" href=\"#heading-\">\r\n    <span class=\"header-link\"><\/span>\r\n  <\/a>\r\n  heading+\r\n<\/h1>\r\n```\r\n\r\n#### Block level renderer methods\r\n\r\n- code(*string* code, *string* language)\r\n- blockquote(*string* quote)\r\n- html(*string* html)\r\n- heading(*string* text, *number*  level)\r\n- hr()\r\n- list(*string* body, *boolean* ordered)\r\n- listitem(*string*  text)\r\n- paragraph(*string* text)\r\n- table(*string* header, *string* body)\r\n- tablerow(*string* content)\r\n- tablecell(*string* content, *object* flags)\r\n\r\n`flags` has the following properties:\r\n\r\n```js\r\n{\r\n    header: true || false,\r\n    align: \'center\' || \'left\' || \'right\'\r\n}\r\n```\r\n\r\n#### Inline level renderer methods\r\n\r\n- strong(*string* text)\r\n- em(*string* text)\r\n- codespan(*string* code)\r\n- br()\r\n- del(*string* text)\r\n- link(*string* href, *string* title, *string* text)\r\n- image(*string* href, *string* title, *string* text)\r\n\r\n### gfm\r\n\r\nType: `boolean`\r\nDefault: `true`\r\n\r\nEnable [GitHub flavored markdown][gfm].\r\n\r\n### tables\r\n\r\nType: `boolean`\r\nDefault: `true`\r\n\r\nEnable GFM [tables][tables].\r\nThis option requires the `gfm` option to be true.\r\n\r\n### breaks\r\n\r\nType: `boolean`\r\nDefault: `false`\r\n\r\nEnable GFM [line breaks][breaks].\r\nThis option requires the `gfm` option to be true.\r\n\r\n### pedantic\r\n\r\nType: `boolean`\r\nDefault: `false`\r\n\r\nConform to obscure parts of `markdown.pl` as much as possible. Don\'t fix any of\r\nthe original markdown bugs or poor behavior.\r\n\r\n### sanitize\r\n\r\nType: `boolean`\r\nDefault: `false`\r\n\r\nSanitize the output. Ignore any HTML that has been input.\r\n\r\n### smartLists\r\n\r\nType: `boolean`\r\nDefault: `true`\r\n\r\nUse smarter list behavior than the original markdown. May eventually be\r\ndefault with the old behavior moved into `pedantic`.\r\n\r\n### smartypants\r\n\r\nType: `boolean`\r\nDefault: `false`\r\n\r\nUse \"smart\" typograhic punctuation for things like quotes and dashes.\r\n\r\n## Access to lexer and parser\r\n\r\nYou also have direct access to the lexer and parser if you so desire.\r\n\r\n``` js\r\nvar tokens = marked.lexer(text, options);\r\nconsole.log(marked.parser(tokens));\r\n```\r\n\r\n``` js\r\nvar lexer = new marked.Lexer(options);\r\nvar tokens = lexer.lex(text);\r\nconsole.log(tokens);\r\nconsole.log(lexer.rules);\r\n```\r\n\r\n## CLI\r\n\r\n``` bash\r\n$ marked -o hello.html\r\nhello world\r\n^D\r\n$ cat hello.html\r\n<p>hello world<\/p>\r\n```\r\n\r\n## Philosophy behind marked\r\n\r\nThe point of marked was to create a markdown compiler where it was possible to\r\nfrequently parse huge chunks of markdown without having to worry about\r\ncaching the compiled output somehow...or blocking for an unnecesarily long time.\r\n\r\nmarked is very concise and still implements all markdown features. It is also\r\nnow fully compatible with the client-side.\r\n\r\nmarked more or less passes the official markdown test suite in its\r\nentirety. This is important because a surprising number of markdown compilers\r\ncannot pass more than a few tests. It was very difficult to get marked as\r\ncompliant as it is. It could have cut corners in several areas for the sake\r\nof performance, but did not in order to be exactly what you expect in terms\r\nof a markdown rendering. In fact, this is why marked could be considered at a\r\ndisadvantage in the benchmarks above.\r\n\r\nAlong with implementing every markdown feature, marked also implements [GFM\r\nfeatures][gfmf].\r\n\r\n## Benchmarks\r\n\r\nnode v0.8.x\r\n\r\n``` bash\r\n$ node test --bench\r\nmarked completed in 3411ms.\r\nmarked (gfm) completed in 3727ms.\r\nmarked (pedantic) completed in 3201ms.\r\nrobotskirt completed in 808ms.\r\nshowdown (reuse converter) completed in 11954ms.\r\nshowdown (new converter) completed in 17774ms.\r\nmarkdown-js completed in 17191ms.\r\n```\r\n\r\n__Marked is now faster than Discount, which is written in C.__\r\n\r\nFor those feeling skeptical: These benchmarks run the entire markdown test suite 1000 times. The test suite tests every feature. It doesn\'t cater to specific aspects.\r\n\r\n### Pro level\r\n\r\nYou also have direct access to the lexer and parser if you so desire.\r\n\r\n``` js\r\nvar tokens = marked.lexer(text, options);\r\nconsole.log(marked.parser(tokens));\r\n```\r\n\r\n``` js\r\nvar lexer = new marked.Lexer(options);\r\nvar tokens = lexer.lex(text);\r\nconsole.log(tokens);\r\nconsole.log(lexer.rules);\r\n```\r\n\r\n``` bash\r\n$ node\r\n> require(\'marked\').lexer(\'> i am using marked.\')\r\n[ { type: \'blockquote_start\' },\r\n  { type: \'paragraph\',\r\n    text: \'i am using marked.\' },\r\n  { type: \'blockquote_end\' },\r\n  links: {} ]\r\n```\r\n\r\n## Running Tests & Contributing\r\n\r\nIf you want to submit a pull request, make sure your changes pass the test\r\nsuite. If you\'re adding a new feature, be sure to add your own test.\r\n\r\nThe marked test suite is set up slightly strangely: `test\/new` is for all tests\r\nthat are not part of the original markdown.pl test suite (this is where your\r\ntest should go if you make one). `test\/original` is only for the original\r\nmarkdown.pl tests. `test\/tests` houses both types of tests after they have been\r\ncombined and moved\/generated by running `node test --fix` or `marked --test\r\n--fix`.\r\n\r\nIn other words, if you have a test to add, add it to `test\/new\/` and then\r\nregenerate the tests with `node test --fix`. Commit the result. If your test\r\nuses a certain feature, for example, maybe it assumes GFM is *not* enabled, you\r\ncan add `.nogfm` to the filename. So, `my-test.text` becomes\r\n`my-test.nogfm.text`. You can do this with any marked option. Say you want\r\nline breaks and smartypants enabled, your filename should be:\r\n`my-test.breaks.smartypants.text`.\r\n\r\nTo run the tests:\r\n\r\n``` bash\r\ncd marked\/\r\nnode test\r\n```\r\n\r\n### Contribution and License Agreement\r\n\r\nIf you contribute code to this project, you are implicitly allowing your code\r\nto be distributed under the MIT license. You are also implicitly verifying that\r\nall code is your original work. `<\/legalese>`\r\n\r\n## License\r\n\r\nCopyright (c) 2011-2014, Christopher Jeffrey. (MIT License)\r\n\r\nSee LICENSE for more info.\r\n\r\n[gfm]: https:\/\/help.github.com\/articles\/github-flavored-markdown\r\n[gfmf]: http:\/\/github.github.com\/github-flavored-markdown\/\r\n[pygmentize]: https:\/\/github.com\/rvagg\/node-pygmentize-bundled\r\n[highlight]: https:\/\/github.com\/isagalaev\/highlight.js\r\n[badge]: http:\/\/badge.fury.io\/js\/marked\r\n[tables]: https:\/\/github.com\/adam-p\/markdown-here\/wiki\/Markdown-Cheatsheet#wiki-tables\r\n[breaks]: https:\/\/help.github.com\/articles\/github-flavored-markdown#newlines\r\n';

