let articleData = '\r\n\u518D\u8FC7\u4E00\u904D`Vue`\u6587\u6863, \u67E5\u6F0F\u8865\u7F3A, \u5BF9\u4EE5\u524D\u81EA\u5DF1\u5FFD\u7565\u7684\u4EE5\u53CA\u81EA\u5DF1\u8BA4\u4E3A\u6BD4\u8F83\u91CD\u8981\u7684\u77E5\u8BC6\u70B9\u505A\u4E0B\u7B14\u8BB0.\r\n\r\n## \u751F\u547D\u5468\u671F\u94A9\u5B50\r\n`created()` \u5F53\u6570\u636E\u7ED1\u5B9A\u5B8C\u6BD5, \u4E8B\u4EF6\u521D\u59CB\u5316\u5B8C\u6BD5\u4F1A\u8C03\u7528\u8FD9\u4E2A\u94A9\u5B50, \u6B64\u65F6`DOM`\u8FD8\u6CA1\u6709\u751F\u6210, \u5982\u679C\u8981\u5BF9`DOM`\u8FDB\u884C\u64CD\u4F5C\u8981\u6CE8\u610F\u8FD9\u4E00\u70B9.\r\n`beforeMount()` \u6B64\u65F6\u6A21\u677F\u7F16\u8BD1\u5B8C\u6210, \u4F46\u662F\u8FD8\u6CA1\u6709\u88AB\u6302\u8F7D\r\n`beforeUpdate()`, \u5F53\u6570\u636E\u6539\u53D8, `DOM`\u91CD\u65B0\u6E32\u67D3\u4E4B\u524D\u4F1A\u4F1A\u8C03\u7528.\r\n`updated()`, \u6570\u636E\u6539\u53D8\u5E76\u4E14\u4ECE\u65B0\u6E32\u67D3\u4E4B\u540E\r\n`beforeDestroy`,  \u8C03\u7528\u65F6\u673A\u5728`this.$destroy()`\u4E4B\u540E, \u6B64\u65F6`watcher``component``event`\u90FD\u8FD8\u597D\u597D\r\n\r\n\u53E6\u5916\u8FD8\u6709\u4E24\u4E2A\u53EA\u5E94\u7528\u4E8E`keep-alive`\u5305\u88F9\u7EC4\u4EF6\u7684\u94A9\u5B50`activated()`\u548C`deactivated()`\r\n`keep-alive`\u610F\u5473\u7740\u5176\u5305\u88F9\u7684\u7EC4\u4EF6\u4F1A\u4FDD\u5B58\u5728\u5185\u5B58\u4E4B\u4E2D, \u518D\u6B21\u5207\u6362\u5230\u8FD9\u4E2A\u7EC4\u4EF6\u9ED8\u8BA4\u4E0D\u4F1A\u88AB\u91CD\u65B0\u6E32\u67D3, \u5982\u679C\u9700\u8981\u91CD\u65B0\u6E32\u67D3\u5219\u53EF\u4EE5\u8003\u8651\u5728\u7EC4\u4EF6\u5207\u6362\u51FA\u53BB\u7684\u65F6\u5019`deactivated()`\u51FD\u6570\u4E2D\u8C03\u7528`this.$destroy()`, \u5728\u5339\u914D`user\/:id`\u8FD9\u79CD\u6839\u636E\u5C55\u793A\u4E0D\u540Cid\u5C55\u793A\u4E0D\u540C\u4FE1\u606F\u7684\u8DEF\u5F84\u7684\u65F6\u5019\u8981\u5C3D\u91CF\u907F\u514D`keep-alive`, \u56E0\u4E3A\u53EF\u80FD\u4F1A\u5BFC\u81F4\u51FA\u73B0\u6240\u6709id\u5BF9\u4E8E\u4E00\u4E2A\u9875\u9762\u7684\u60C5\u51B5.\r\n\r\n\u53E6\u5916\u4ECE\u6587\u6863\u7684\u7EC4\u4EF6\u751F\u547D\u5468\u671F\u56FE\u4E2D\u53EF\u4EE5\u770B\u51FA\u6765, \u5F53\u4E00\u4E2A\u7EC4\u4EF6\u4E2D\u65E2\u6709`template`\u53C8\u6709`el`\u65F6, `el`\u4F1A\u88AB\u8986\u76D6.\r\n\r\n## \u8FC7\u6EE4\u5668\r\nvue 2 \u4E2D\u8FC7\u6EE4\u5668\u53EA\u80FD\u8FD9\u6837`{{data | filter}}` \u4E0D\u80FD\u5728\u6307\u4EE4\u4E2D\u5B9E\u73B0\u8FD9\u6837\u7684\u884C\u4E3A, \u5982\u679C\u8981\u5728\u6307\u4EE4\u4E2D\u5B9E\u73B0\u540C\u6837\u7684\u884C\u4E3A\u5EFA\u8BAE\u4F7F\u7528\u8BA1\u7B97\u5C5E\u6027,\r\n```\r\n{\r\n  filters: {\r\n    cap (val) {\r\n\r\n    },\r\n  },\r\n}\r\n```\r\n## \u6570\u7EC4\u4FEE\u6539\r\n`vm.items[indexOfItem] = newValue`\u4EE5\u53CA `vm.items.length = newLength`\u4E0D\u4F1A\u5F15\u8D77\u89C6\u56FE\u66F4\u65B0, \u6211\u4EEC\u53EF\u4EE5\u8FD9\u6837\u5199\r\n``` JavaScript\r\nVue.set(item, \'data\', newValue);\r\nitem.splice(indexOfItem, 1, newValue);\r\n\r\n<!-- \u8BBE\u7F6E\u957F\u5EA6 -->\r\nitem.splice(length);\r\n```\r\n\r\n## \u4E8B\u4EF6\u5904\u7406\u5668\r\n\u5F53\u4E00\u4E2A`viewModel`\u88AB\u9500\u6BC1\u65F6, \u6240\u6709\u4E8B\u4EF6\u5904\u7406\u5668\u90FD\u4F1A\u88AB\u81EA\u52A8\u5220\u9664. -> `keep-alive`?\r\n`capture`\u4FEE\u9970\u7B26\u4E8B\u4EF6\u6355\u83B7, `self` \u4FEE\u9970\u7B26, \u53EA\u4F1A\u5728\u5143\u7D20\u672C\u8EAB\u89E6\u53D1\u4E8B\u4EF6, \u4E0D\u4F1A\u5728\u5B50\u5143\u7D20\u89E6\u53D1\u4E8B\u4EF6. -> \u611F\u89C9\u5199modal\u7EC4\u4EF6\u4F1A\u6709\u7528\r\n\u81EA\u5B9A\u6309\u952E\u4FEE\u9970\u540D`Vue.config.keyCodes.f1 = 112`\r\n\r\n## \u8868\u5355\u7ED1\u5B9A\r\n`v-model`\u53EA\u652F\u6301\u8868\u5355\u5143\u7D20\u4EE5\u53CA\u67D0\u4E9B\u7EC4\u4EF6, \u6570\u636E\u7ED1\u5B9A\u4E3A\u53CC\u5411\u7ED1\u5B9A, \u5982\u679C\u8981\u7528\u4E8E`contenteditable`\u5C5E\u6027\u5143\u7D20, \u90A3\u5C31\u9700\u8981\u5C01\u88C5\u7EC4\u4EF6,\r\n`textarea`\u5143\u7D20\u7684\u63D2\u503C\u5E76\u6CA1\u6709\u4EC0\u4E48\u4E71\u7528, \u7528`v-model`\r\n';


export default {
  body: articleData,
  created_at: '',
};



